  display: block;
  height: 831px;
  width: 805px;
  background-color: #0d161e; }

.tabBar {
  width: 805px;
  max-width: 100%;
  height: 26px;
  margin: 0;
  position: relative; }
  .tabBar .tabBarSlider {
    width: calc(100% - 52px);
    margin: 0 26px;
    overflow: hidden;
    position: absolute; }
  .tabBar .tabBarLeft,
  .tabBar .tabBarRight {
    width: 26px;
    height: 26px;
    display: block;
    position: absolute; }
  .tabBar .tabBarRight {
    right: 0; }

#tabSwitch {
  display: flex;
  flex-direction: row;
  height: 26px;
  width: 1083px;
  margin: 0;
  padding: 0; }
  #tabSwitch li {
    display: flex;
    flex-direction: row;
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: center; }
    #tabSwitch li span {
      padding: 6px 0; }
    #tabSwitch li.active span {
      background-position: left bottom; }
    #tabSwitch li .left {
      display: block;
      height: 14px;
      width: 19px; }
    #tabSwitch li .center {
      display: block;
      height: 14px;
      width: 19px; }
    #tabSwitch li .right {
      display: block;
      height: 14px;
      width: 19px; }

#tab {
  display: block;
  height: 804px;
  width: 804px;
  position: relative;
  border: 1px solid #473927;
  border-left: none;
  border-top: none; }
  #tab .cell {
    display: block;
    height: 66px;
    position: absolute;
    width: 66px;
    border: 1px solid #473927;
    border-right: none;
    border-bottom: none; }
    #tab .cell img {
      height: 100%;
      width: 100%; }
  #tab .doublet {
    background-color: red; }
